.img {
    height: 10%;
    max-width: 100%;

}



img {
    width: 100%;
    height: auto;
}

.owl-next:hover,
.owl-pre:hover {
    border: none;
    background-color: none;
}

button:hover {
    outline: none !important;
}

.owl-next span,
.owl-prev span {
    font-size: 1.5vw;
    background-color: yellowgreen;
    padding: 25px;
    color: white;
    border-radius: 50%;


    text-align: center;
    text-decoration: none !important;
}

.img-wrapper {
    border-radius: 10px;
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;



}

.inner-img {
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;

}

.container-fluid button {
    outline-style: none !important;
}

.owl-dots {
    display: none !important;
}

.img-wrapper {
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
    transition: 0.3s;

}

.img-wrapper:hover {
    box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2);
}

.container {
    padding: 2px 0px;
    max-width: 200px;
    text-align: justify;
    text-justify: inter-word;
}

.owl-item {

    margin-bottom: 15px;
    box-sizing: border-box;
}