.Con::after {
	content: '';
	clear: both;
	display: table;
	width: 100%;
	text-align: center;
}

.back {

	background: linear-gradient(180deg,
			#ffffff,
			#ffffff,
			#b0efe1,
			#88eee7,
			#6de9f8,
			#81deff,
			#a4d1ff,
			#c3c3f6);
}

@media only screen and (min-width: 496px) {
	#Rig {

		text-align: center;
	}
}

.form-group .form-control {


	font-size: 1.5vw !important;

}

label {
	font-size: 1.5vw !important;
}


#Lef {
	margin-top: 140px !important;
}

#Rig {
	margin-top: 100px !important;
}

.btn-dark {
	border-radius: 8px;
}

.btn-dark:hover {
	color: white !important;
}

.reds {
	color: rgb(221, 13, 13);
	font-size: 1.5vw;
}

#Lef {
	float: left;
	width: 58%;
	margin-top: 4%;
	height: auto;
	padding: 2% 2% 2% 8.5% !important;
}

#cols {
	color: rgb(223, 123, 33);
}

#Rig {
	float: left;
	width: 38%;
	padding: 6% 5% 2% 2% !important;
	height: auto;
	text-align: left;

}

#Lef,
#Rig {


	padding: 0px 10px;
}

.btn {
	background-color: rgb(16, 25, 40);
	/* White */
	border: none;
	color: rgb(253, 252, 252);
	padding: 10px 20px;
	text-align: center;
	text-decoration: none;
	display: inline-block;
	border-radius: 10px;
	font-size: 1vw;
}